body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* padding-top: 23px; */
  background-color: #daf0ff !important;
  padding: 23px;
  overflow-x: hidden !important;
}

.App {
  overflow-x: hidden !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ant-table-cell {
  /* max-width: min-content !important; */
  max-width: 60px;
  padding: 16px 6px !important;
  font-size: 12px !important;
}

.ant-table-cell:nth-child(14) {
  display: none !important;
}

header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 23px;
  flex-wrap: wrap;
  margin: 23px 0px;
  background-color: white;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 23px;
}

header .search {
  width: 100%;
  max-width: 600px;
}

header .search .ant-input-affix-wrapper {
  border-top-left-radius: 23px !important;
  border-bottom-left-radius: 23px !important;
  background-color: rgba(245, 247, 255, 0.4) !important;
}

header .search .ant-input-group-addon button {
  border-top-right-radius: 23px !important;
  border-bottom-right-radius: 23px !important;
  background-color: rgba(245, 247, 255, 0.4) !important;
}

header .search .ant-input-affix-wrapper,
.ant-input-group-addon button,
.popup button {
  height: 45px !important;
}

.popup button {
  border-radius: 23px !important;
}

.ant-input-group-addon button {
  width: 45px !important;
}

.sponsored {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px 0%;
  flex-wrap: wrap;
  gap: 10px;
  border-bottom: 2px solid black;
  padding-bottom: 43px;
}

.sponsored .box {
  width: 24%;
  height: auto;
  display: flex;
  gap: 10px;
  /* border: 1px solid black; */
  align-items: center;
  border-radius: 23px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background-color: white;
}

.sponsored img {
  width: 100px;
  height: 100px;
  border-radius: 23px;
}

.ant-table {
  /* margin: 10px 23px !important; */
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

:where(.css-dev-only-do-not-override-amq5gd).ant-table-wrapper .ant-table-container table>thead>tr:first-child>*:first-child {
  border-start-start-radius: 17px !important;
}

:where(.css-dev-only-do-not-override-amq5gd).ant-pagination .ant-pagination-item-active {
  background-color: #1677ff !important;
  color: white !important;
}

:where(.css-dev-only-do-not-override-amq5gd).ant-pagination .ant-pagination-item-active a {
  color: white !important;
}